body {
  padding: 0;
  margin: 0;
  background-color: #e8eaec;
}

h1 {
  margin: 0;
}

.w-100 {
  width: 100%;
}

.w-50 {
  width: 50%;
}

.w-25 {
  width: 25%;
}

.h-100 {
  height: 100%;
}

.hpx-100 {
  height: 100px;
}

.bg-button-normal {
  background-color: #dfdfdf;
}

.bg-button-orange {
  background-color: #f69148;
}

.calculator {
  display: grid;
  grid-template-columns: 1fr 43.75rem 1fr;
  grid-template-rows: 100px 20px 37.5rem 100px;
  grid-template-areas:
    'nav nav nav'
    '. . .'
    '. calculator .'
    '. . .';
}

.App {
  grid-area: calculator;
}

.Display {
  background-color: #848492;
  color: white;
  text-align: right;
  padding: 0 2rem 0 0;
  font-weight: 900;
  font-size: 32px;
}

.btn {
  text-align: center;
  font-weight: 400;
  color: black;
  font-size: 2rem;
  text-decoration: none;
  border: 1px solid #d0d0d0;
}
